import { model, Model } from '../../core.service';
import type { ErrorResult } from './errortip';

export enum ErrorTipTab {
    SOLUTION = 'solution',
    EXCEPTION = 'exception'
}

@model()
export class ErrorTipModel extends Model {
    public static xtype = 'report.model.errortip';

    public state () {
        return {
            showIndex: ErrorTipTab.SOLUTION,
            errorResult: {} as ErrorResult,
        };
    }

    public actions = {
        changeTabPanel: (tab: ErrorTipTab) => {
            this.model.showIndex = tab;
        },
        setErrorResult: (errorResult: ErrorResult) => {
            const { solution, ...arg } = errorResult;

            this.model.errorResult = {
                solution: solution || BI.i18nText('Fine-Report_Preview_Fail_Tip'),
                ...arg,
            };
        },
    }
}
